import React from "react";
import { useParams } from "react-router-dom";
import Container from "../../common/container";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import { workInfo } from "../../data/workInfo";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { Link } from "react-router-dom";
import Workthumb from "../../common/work-thumb";
import Refresh from "./Refresh";
import HomeIcon from "@material-ui/icons/Home";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import Typography from "@material-ui/core/Typography";
import "./style.css";

export default function Index() {
  const [data, setDatas] = React.useState(workInfo);
  const [page, setPage] = React.useState(0);

  const { id } = useParams();

  React.useEffect(() => {
    const list = () => {
      return data.find((e) => e.id == id);
    };

    let nextFun = () => {
      let indNum = data.findIndex((e) => e.id == id);
      
      return indNum;
    };

    setPage(nextFun);

    setDatas(list);
  }, []);

  React.useEffect(() => {
    const list = () => {
      return workInfo.find((e) => e.id == id);
    };

    const newList = () => {
      let indNum = workInfo.findIndex((e) => e.id == id);

      if (indNum < 1) {
        return (indNum = page);
      }

      if (indNum > 14) {
        return (indNum = page);
      }

      return indNum;
    };

    setPage(newList);

    setDatas(list);
  }, [id]);

  return (
    <>
      <section className="preview-column">
        <div className="bg-color">
          <section className="preview-header">
            <div className="action-btn">
              <IconButton to={`/preview/${page}`} component={Link}>
                <ArrowBackIosIcon />
              </IconButton>

              <IconButton to="/" component={Link}>
                <HomeIcon  />
              </IconButton>

              <IconButton to={`/preview/${page + 2}`} component={Link}>
                <ArrowForwardIosIcon />
              </IconButton>
            </div>
          </section>
          <div className="container-box scroll-box relative">{data.component}</div>
        </div>
        <div className="left-border preview-scroll-box">
          <div className="preview-info-contener">
            <header className="title">{data.comp}</header>
            <small className="text-column">
              <span className="label">Work Type: </span>
              {data.type}
            </small>
            <br />
            <small className="text-column">
              <span className="label">Tools:</span>
              {data.tool && data.tool.map((item) => item).join(", ")}
            </small>
            <br />
            <small className="text-column">
              <span className="label">Discription:</span> {data.info}
            </small>
          </div>
          <header className="other-work">Other Work</header>
          <div className="work-slider">
            <Workthumb boxArray={workInfo} />
          </div>
        </div>
      </section>
    </>
  );
}
